<template>
  <div class="contact">
    <div class="container-form">
      <b-form @submit="onSubmit" @reset="onReset" v-if="show">
        <b-form-group
          id="input-group-1"
          label="Email address:"
          label-for="input-1"
          description="We'll never share your email with anyone else."
        >
          <b-form-input
            id="input-1"
            v-model="form.email"
            type="email"
            required
            placeholder="Enter email"
          ></b-form-input>
        </b-form-group>

        <b-form-group id="input-group-2" label="Your Name:" label-for="input-2">
          <b-form-input
            id="input-2"
            v-model="form.name"
            required
            placeholder="Enter name"
          ></b-form-input>
        </b-form-group>

        <b-form-group id="input-group-3" label="Food:" label-for="input-3">
          <b-form-select
            id="input-3"
            v-model="form.food"
            :options="foods"
            required
          ></b-form-select>
        </b-form-group>

        <b-form-group id="input-group-4">
          <b-form-checkbox-group v-model="form.checked" id="checkboxes-4">
            <b-form-checkbox value="me">Check me out</b-form-checkbox>
            <b-form-checkbox value="that">Check that out</b-form-checkbox>
          </b-form-checkbox-group>
        </b-form-group>

        <b-button type="submit" variant="primary">Submit</b-button>
        <b-button type="reset" variant="danger">Reset</b-button>
      </b-form>
    </div>
    <div class="geolocation">
      <b-button type="button" variant="primary" v-on:click="geolocation">Take my location</b-button>
      <div><a id="map-link"></a></div>
    </div>
  </div>
</template>

<script>
import {
  BButton,
  BCard,
  BForm,
  BFormGroup,
  BFormCheckboxGroup,
  BFormCheckbox,
  BFormSelect,
  BFormInput
} from 'bootstrap-vue'

export default {
  components: {
    BButton,
    BCard,
    BForm,
    BFormCheckboxGroup,
    BFormCheckbox,
    BFormSelect,
    BFormInput,
    BFormGroup
  },

  data () {
    return {
      form: {
        email: '',
        name: '',
        food: null,
        checked: []
      },
      foods: [{ text: 'Select One', value: null }, 'Carrots', 'Beans', 'Tomatoes', 'Corn'],
      show: true
    }
  },

  methods: {
    onSubmit (evt) {
      evt.preventDefault()
      alert(JSON.stringify(this.form))
    },

    geolocation () {
      if (navigator.geolocation) {
        (function () {
          const mapLink = document.querySelector('#map-link')

          function success (position) {
            const lat = position.coords.latitude
            const lang = position.coords.longitude

            mapLink.href = `https://www.openstreetmap.org/#map=18/${lat}/${lang}`
            mapLink.textContent = `Latitude: ${lat} °, Longitude: ${lang} °`
          }

          function error () {
            console.log('Unable to retrieve your location')
          }

          if (!navigator.geolocation) {
            console.log('Geolocation is not supported by your browser')
          } else {
            console.log('Locating…')
            navigator.geolocation.getCurrentPosition(success, error, { maximumAge: 60000, timeout: 5000, enableHighAccuracy: true })
          }
        })()
      }
    },

    onReset (evt) {
      evt.preventDefault()
      // Reset our form values
      this.form.email = ''
      this.form.name = ''
      this.form.food = null
      this.form.checked = []
      // Trick to reset/clear native browser form validation state
      this.show = false
      this.$nextTick(() => {
        this.show = true
      })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '../../assets/styles/app';

.contact {
  height: 90%;
  padding-top: 10%;
  display: grid;

  .container-form {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .geolocation {
    padding: 2%;
  }
}

.router-link-custom {
  &:hover {
    color: #ffffff;;
  }
}
</style>



